<template>
    <div>
        <div class="close">
            <i class="iconfont iconicon-test"></i>
        </div>
        <div class="logo">
            <i class="iconfont iconnew"></i>
        </div>
        <AuthInput 
        type="text" 
        placeholder="用户名/手机号码" 
        rule="^1[3456789]\d{9}$"
        errMsg="请输入正确的手机号码"
        @valChanged="setUserName" />
        <AuthInput 
        type="password" 
        placeholder="密码" 
        rule="^\d{6}$"
        errMsg="密码有误"
        @valChanged="setPassword"/>
        <AuthBtn btnTxt="登  录" @btnClicked="login"/>
    </div>
</template>

<script>
import AuthInput from '@/components/AuthInput';
import AuthBtn from '@/components/AuthBtn';
export default {
    data(){
       return{
           username:'',
           password:''
       }
    },
    components:{
        AuthInput,
        AuthBtn
    },
    methods:{
        setUserName(username){
            this.username = username;
        },
        setPassword(password){
            this.password = password;
        },
        login(){
            const that = this;
            this.$axios({
                url:'http://liangwei.tech:3000/login',
                method:'post',
                data:{
                    username:that.username,
                    password:that.password
                }
            }).then((res)=>{
                console.log(res.data.data);
                if(res.data.message != '登录成功')
                    this.$toast.fail(res.data.message);
                else
                    // 本地储存
                    localStorage.setItem('token',res.data.data.token);
                    localStorage.setItem('user',JSON.stringify(res.data.data.user));
                    // 跳转到个人中心
                    this.$router.push({name: 'personalCenter'});
            
            }).catch((err)=>{
                console.log(err);
                //this.$toast.fail(err.responseText.msg);
            });
        }
    }
}
</script>

<style lang="less" scoped>
.close{
    padding:6.667vw;//24
    .iconicon-test{
        font-size: 7.5vw;
        color: #c0c0c0;
    }
}
.logo{
    height: 27.778vw;
    line-height: 27.778vw;
    text-align: center;
    .iconnew{
        font-size: 36.111vw;
        color: #ffc0cb;
    }
}
</style>