<template>
     <div>
         <div class="close">
            <i class="iconfont iconicon-test"></i>
        </div>
        <div class="logo">
            <i class="iconfont iconnew"></i>
        </div>
        <AuthInput 
        type="text" 
        placeholder="用户名/手机号码" 
        rule="^1[3456789]\d{9}$"
        errMsg="手机号码有误"
        @valChanged="setUserName" />
        <AuthInput 
        type="text" 
        placeholder="昵称" 
        rule="^[\u4E00-\u9FA5\uF900-\uFA2D|\w]{2,20}$"
        errMsg="昵称有误"
        @valChanged="setNickName" />
        <AuthInput 
        type="password" 
        placeholder="密码" 
        rule="^\d{6}$"
        errMsg="密码有误"
        @valChanged="setPassword"/>
        <AuthBtn btnTxt="注  册" @btnClicked="register"/>
     </div>
</template>

<script>
import AuthInput from '@/components/AuthInput';
import AuthBtn from "@/components/AuthBtn";

export default {
    data(){
        return{
            username:'',
            nickname:'',
            password:''
        }
    },
    components:{
        AuthInput,
        AuthBtn
    },
    methods:{
       setUserName(username){
           this.username = username;

       },
       setNickName(nickname){
           this.nickname = nickname; 
       },
       setPassword(password){
           this.password = password;
       },
       register(){
           const that = this;
           this.$axios({
            //    http://liangwei.tech:3000  http://127.0.0.1:3000
               url:'http://liangwei.tech:3000/register',
               method:'post',
            //    headers: {处理http预请求
            //        'Content-Type': 'application/x-www-form-urlencoded'
            //    },
               data:{
                   username:that.username,
                   nickname:that.nickname,
                   password:that.password
               }
           }).then((res)=>{
               console.log(res);
               if(res.data.message != '注册成功')
                  this.$toast.fail(res.data.message);
               else
                   localStorage.setItem('user',JSON.stringify(res.data.data));
                   this.$router.push({name: 'personalCenter'});
               
           }).catch((err)=>{
               console.log(err);
               //this.$toast.fail(err.responseText.msg);
           });
       }
    }
}
</script>

<style lang="less" scoped>
.close{
    padding:6.667vw;//24
    .iconicon-test{
        font-size: 7.5vw;
        color: #c0c0c0;
    }
}
.logo{
    height: 27.778vw;
    line-height: 27.778vw;
    text-align: center;
    .iconnew{
        font-size: 36.111vw;
        color: #ffc0cb;
    }
}
</style>