<template>
    <div>
        <div class="personal-center">
            <div class="user-pic">
                <img :src="headImg" alt="">
            </div>
            <div class="user-info">
                <h5><i class="iconfont" :class="gender == 1 ? 'iconxingbienv col_pink':'iconxingbienan col_blue'" ></i><span>{{nickname}}</span></h5>
                <span>{{createDate}}</span>
            </div>
            <div class="to-edit" @click="toEdit">
                <i class="iconfont iconjiantou1"></i>
            </div>
        </div>
        <div class="myself">
            <AboutMe name="我的关注" desc="关注的用户" @toClick="toFollow"/>
            <AboutMe name="我的跟帖" desc="跟帖/回复"/>
            <AboutMe name="我的收藏" desc="文章/视频"/>
            <AboutMe name="设置" desc=""/>
        </div>
    </div>
</template>

<script>
import AboutMe from '@/components/AboutMe'
export default {
    data(){
        return{
            nickname:'',
            gender:'',
            headImg:'',
            createDate:''
        }
    },
    components:{
        AboutMe
    },
    methods:{
        getUserInfo(){
           //从本地储存中获取
           const user = JSON.parse(localStorage.getItem('user'));
           console.log(user);
           this.nickname = user.nickname;
           this.gender = user.gender;
           console.log(this.gender);
           this.headImg = 'http://liangwei.tech:3000'+user.head_img;
           this.createDate = user.create_date ? user.create_date.split('T')[0] :'';
        },
        toFollow(){
            this.$router.push({name:'myFollow'});
        },
        toEdit(){
            this.$router.push({name:'editInfo'});
        }
    },
    mounted(){
         this.getUserInfo();
    }
}
</script>

<style lang="less" scoped>
.personal-center{
    display: flex;
    height: 36.111vw;
    line-height: 36.111vw;
    border-bottom: 1.389vw solid #E4E4E4;
    .user-pic{
        flex:2;
        padding-left: 6.111vw;
        img{
            width: 19.444vw;
            height: 19.444vw;
            border-radius: 50%;
            vertical-align: middle;
        }
    }
    .user-info{
        flex:7;
        line-height: 1.15;
        padding:13.889vw 0 11.111vw 0;
        padding-left: 4.167vw;
        h5{
           padding-bottom: 2.222vw;
           font-size: 4.444vw;
           font-weight: 400;
           color: #333333;
           .iconfont{
               //color: #75B9EB;
               font-size: 4.444vw;
           }
        }
        >span{
           font-size: 3.889vw;  
           color: #A1A1A1;
        }
    }
    .to-edit{
        flex:1;
    }
}
.myself{
    padding-left:6.111vw;//22
}
.col_pink{
   color:#ffc0cb;
}
.col_blue{
    color: #75B9EB;
}
</style>