<template>
     <div>
         <div class="editInfo">
             <div class="header">
                 <Myhead headName="编辑资料" @tojemp="toBack"/>
             </div>
             <div class="user-pic">
                <img :src="headImg" alt="">
            </div>
         </div>
         <div class="info-set">
            <AboutMe name="昵称" :desc="nickname"/>
            <AboutMe name="密码" desc="******"/>
            <AboutMe name="性别" :desc="gender"/>
         </div>
     </div>
</template>

<script>
import Myhead from '@/components/MyHeader'
import AboutMe from '@/components/AboutMe'
export default {
    data(){
        return{
           nickname:'',
           password:'',
           gender:'',
           headImg:''
        }
    },
    components:{
        Myhead,
        AboutMe
    },
    methods:{
        toBack(){
            this.$router.back();
        }
    },
    mounted(){
        const user = JSON.parse(localStorage.getItem('user'));
        this.nickname = user.nickname;
        this.password = user.password;
        this.gender = user.gender == 1 ? '女':'男';
        this.headImg = 'http://liangwei.tech:3000'+user.head_img;
    }
}
</script>

<style lang="less" scoped>
.editInfo{
    height: 52.778vw;
    .user-pic{
    text-align: center;
        img{
            width: 19.444vw;
            height: 19.444vw;
            border-radius: 50%;
            vertical-align: middle;
        }
    }
}
.info-set{
    padding-left:6.111vw;//22
}

</style>